

import React, { FC, ReactElement, useEffect } from 'react'
import Box from '@mui/material/Box';
import MenuIcon from '@mui/icons-material/Menu';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

import Card from '@mui/material/Card';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';

import CircularProgress from '@mui/material/CircularProgress';

import './ThingCard.css'

import * as saved from './SavedStuff'
import * as utils from './Utils'
import * as registry from './ChangeRegistry'
import * as  mqtt from './MqttClient';
import * as  helpers from './Helpers';
import * as  netStatus from './NetStatus';

import * as types from './Types';
import { RoomPreferencesSharp } from '@mui/icons-material';


export interface helpLine {
    theCommand: string
    argCount: number
    description: string
    stars: number
}

// parseCmd will parse a line in the format: [cmd1 cmd2] +1 comment comment * 
export function parseCmd(cmd: string): helpLine {
    let h: helpLine = {
        theCommand: '',
        argCount: 0,
        description: '',
        stars: 0
    }
    const i1 = cmd.indexOf('[')
    const i2 = cmd.indexOf(']')
    if (i1 < 0 || i1 < 0) {
        return h // ['', 0, '']
    }
    let theCommand = cmd.slice(i1 + 1, i2)
    theCommand = theCommand.trim()
    console.log("parseCmd cmd", theCommand)
    h.theCommand = theCommand

    let theRest = cmd.slice(i2 + 1)
    theRest = theRest.trim()
    console.log("parseCmd theRest", theRest)
    while (theRest.endsWith('*')) {
        theRest = theRest.slice(0, theRest.length - 1)
        h.stars++
    }
    if (theRest.startsWith('+')) {
        let c = theRest.charCodeAt(1)
        c = c - "0".charCodeAt(0)
        h.argCount = c
        theRest = theRest.slice(2)
    }
    h.description = theRest.trim()

    return h
}


type State = {
    config: saved.ThingConfig
    pendingCommandNonc: string,// a correlation id for an mqtt publish
    returnValue: string,
    pendingHelpNonc: string,
    uniqueid: string
}

export interface Props {

    showPubkey: boolean
    showAdminKey: boolean
    globalConfig: saved.GlobalConfig
    config: saved.ThingConfig
    index: number
}

type loadedInfo = {
   // version: string,
    pubk: string,
    adminHint: string
}

const DefaultLoadedInfo: loadedInfo = {
   // version: '',
    pubk: '',
    adminHint: ''
}

export const ThingCard: FC<Props> = (props: Props): ReactElement => {

    const defaultState: State = {
        config: props.config,
        pendingCommandNonc: '',
        returnValue: '-none-',
        pendingHelpNonc: '',
        uniqueid: utils.randomString(24),
    }

    const [state, setState] = React.useState(defaultState);

    // this is for the right menu
    const [menuUp, setMenuUp] = React.useState(null);
    // this for the left side command select
    const [selectUp, setSelectUp] = React.useState(null);

    let rparts: string[] = []
    const [commands, setCommands] = React.useState(rparts);

    // need about, pubk, and admin hints
    const [info, setInfo] = React.useState(DefaultLoadedInfo);

    //let args = new Map<string, string>() // state?
    let def: types.LooseObject = {}
    const [args, setArgs] = React.useState(def);

    const gotReturnValue = (name: string, arg: any) => {

        let options: types.LooseObject = arg

        var message: string = options.message.toString('utf8')

        //console.log("ThingCard got message", message)
        registry.RemoveSubscripton(name)
        const newState: State = {
            ...state,
            returnValue: message,
            pendingCommandNonc: ''
        }
        setState(newState)
    }

    const gotHelpValue = (name: string, arg: any) => {

        let options: types.LooseObject = arg

        var message: string = options.message.toString('utf8')

        console.log("ThingCard got gotHelpValue", message)
        registry.RemoveSubscripton(name)
        const newState: State = {
            ...state,
            pendingHelpNonc: ''
        }
        setState(newState)
        let rparts = message.split('\n')
        setCommands(rparts);
    }

    // const receiveVersionInfo = (name: string, arg: any) => {

    //     let options: types.LooseObject = arg
    //     var message: string = options.message.toString('utf8')

    //     const newState: loadedInfo = {
    //         ...info,
    //         version: message,
    //     }
    //     setInfo(newState)
    // }
    const receivePubkInfo = (name: string, arg: any) => {

        let options: types.LooseObject = arg
        var message: string = options.message.toString('utf8')

        const newState: loadedInfo = {
            ...info,
            pubk: message,
        }
        setInfo(newState)
    }
    const receiveAdminHintInfo = (name: string, arg: any) => {

        let options: types.LooseObject = arg
        var message: string = options.message.toString('utf8')

        const newState: loadedInfo = {
            ...info,
            adminHint: message,
        }
        setInfo(newState)
    }

    function heartBeat() {

        if (netStatus.GlobalNetStatus !== 'Connected') {
            return
        }

        // if (info.version === '') {
        //     // order it up
        //     // console.log('heartBeat ordering version ', props.index)
        //     const nonc = "_" + props.index + "_" + utils.randomString(24)
        //     registry.SetSubscripton(nonc, receiveVersionInfo)
        //     mqtt.Publish('version', state.config.longname, nonc)
        // }
        if (info.pubk === '') {
            // order it up
            // console.log('heartBeat ordering pubk ', props.index)
            const nonc = "_" + props.index + "_" + utils.randomString(24)
            registry.SetSubscripton(nonc, receivePubkInfo)
            mqtt.Publish('get pubk', state.config.longname, nonc)
        }
        let needAdminHint = info.adminHint === ''
        // if there's no pub key or pub key is '-none-' or otherwise not 43 chars 
        // then we don't need admin hints. 
        if (info.pubk !== '' && info.pubk.length < 40) {
            needAdminHint = false
        }
        if (needAdminHint) {
            // order it up
            // console.log('heartBeat ordering about ', props.index)
            const nonc = "_" + props.index + "_" + utils.randomString(24)
            registry.SetSubscripton(nonc, receiveAdminHintInfo)
            mqtt.Publish('get admin hint', state.config.longname, nonc)
        }

    }


    useEffect(() => {

        utils.AddHeartbeatCallback('index' + props.index, heartBeat)

        if (state.pendingCommandNonc !== '') {
            // console.log("dev card sending ", state.pendingCommandNonc)
            registry.SetSubscripton(state.pendingCommandNonc, gotReturnValue)
            // check to not do this twice? A: later in the retry logic
            let cmd = state.config.commandString
            for ( let i = 0; i < 10; i++) {
                // console.log("args",args)
                let argText: string = args['' + i]
                if (argText !== undefined) {
                    if (argText.includes(' ')) {
                        argText = '"' + argText + '"' // todo: atw  escape quotes
                    }
                    cmd = cmd + ' ' + argText
                } else { 
                    break
                }
            }
            mqtt.Publish(cmd, state.config.longname, state.pendingCommandNonc)
        }

        if (state.pendingHelpNonc !== '') {
            // console.log("dev card sending help ", state.pendingHelpNonc)
            registry.SetSubscripton(state.pendingHelpNonc, gotHelpValue)
            // check to not do this twice? A: later in the retry logic
            mqtt.Publish('help', state.config.longname, state.pendingHelpNonc)
        }
    })

    function textClicked(e: React.ChangeEvent<HTMLInputElement>) {
        const str = e.currentTarget.value
        console.log("textClicked", str)
    }

    function argTextClicked(e: React.ChangeEvent<HTMLInputElement>) {
        const str = e.currentTarget.value
        let id = e.currentTarget.id
        const newArgs = {
            ...args
        }
        newArgs[id] = str
        setArgs(newArgs)
    }

    function changeThingName(e: any) { // on the blur unfocus
        const str: string = e.target.value
        console.log("new Thing name", str)
        let newName: string = str
        let c = saved.getThingsConfig()
        c.things[props.index].longname = newName
        saved.setThingsConfig(c)
        const newState: State = {
            ...state,
            config: {
                ...state.config,
                longname: newName,
            },
        }
        setState(newState)
        setCommands([])
    }

    function doCommandAttempt() {

        let nonc = utils.randomString(24)
        // console.log("doCommandAttempt nonc", nonc)
        let newState: State = {
            ...state,
            pendingCommandNonc: nonc,
            returnValue: "-pending-"
        }
        setState(newState)
    }

    const handleMenuClick = (event: any) => {
        console.log("handleMenuClick", event)
        setMenuUp(event.currentTarget);
    };

    const handleMenuDuplicate = (event: any) => {

        setMenuUp(null);

        const newConfig: saved.ThingConfig = {
            ...state.config
        }
        let c = saved.getThingsConfig()
        //c.things.push(newConfig)
        c.things.splice(props.index, 0, newConfig);
        //xx xx props.index
        saved.setThingsConfig(c)
        // now notify our parent
        registry.PublishChange("ThingsChangeNotification", "nothing")
    };

    const handleMenuDelete = (event: any) => {

        setMenuUp(null);

        let c = saved.getThingsConfig()
        c.things.splice(props.index, 1)
        saved.setThingsConfig(c)

        // now notify our parent
        registry.PublishChange("ThingsChangeNotification", "nothing")

    };

    const handleMenuUp = (event: any) => {

        setMenuUp(null);

        const newConfig: saved.ThingConfig = {
            ...state.config
        }
        let c = saved.getThingsConfig()

        if (props.index > 0) {
            let me = c.things.splice(props.index, 1);
            c.things.splice(props.index - 1, 0, me[0]);
        }

        saved.setThingsConfig(c)
        // now notify our parent
        registry.PublishChange("ThingsChangeNotification", "nothing")

    };

    const handleMenuDown = (event: any) => {

        setMenuUp(null);

        const newConfig: saved.ThingConfig = {
            ...state.config
        }
        let c = saved.getThingsConfig()

        if (props.index + 1 < c.things.length) {
        let me = c.things.splice(props.index, 1);
        c.things.splice(props.index + 1, 0, me[0]);
        }

        saved.setThingsConfig(c)
        // now notify our parent
        registry.PublishChange("ThingsChangeNotification", "nothing")

    };


    const handleClose = () => {
        setMenuUp(null);
    };

    const handleSelectClick = (event: any) => {
        console.log("handleSelectClick", event)
        setSelectUp(event.currentTarget);
        // setOpenNewPost(true);
        //  handleClose()
        if (commands.length === 0 && state.pendingHelpNonc === '') {
            let nonc = utils.randomString(24)
            console.log("do load help nonc", nonc)
            let newState: State = {
                ...state,
                pendingHelpNonc: nonc,
            }
            setState(newState)
        }
    };

    const handleSelectSelect = (event: any) => {
        console.log("handleSelectSelect", event)
        console.log("handleSelectSelect id", event.target.id)
        let i = +event.target.id
        let cmd = commands[i]

        let h: helpLine = parseCmd(cmd)
        if (h.theCommand === '') {
            return;//bad selection
        }

        let c = saved.getThingsConfig()
        c.things[props.index].commandString = h.theCommand
        c.things[props.index].cmdArgCount = h.argCount
        c.things[props.index].cmdDescription = h.description
        c.things[props.index].stars = h.stars
        saved.setThingsConfig(c)

        // also issue the command
        if (h.argCount == 0) {

            let nonc = utils.randomString(24)

            const newState: State = {
                ...state,
                config: {
                    ...state.config,
                    commandString: h.theCommand,
                    cmdArgCount: h.argCount,
                    cmdDescription: h.description,
                    stars: h.stars
                },
                pendingCommandNonc: nonc, // also issue the command
                returnValue: "-pending-"
            }

            setState(newState)
        }
        setSelectUp(null);
    };

    const handleSelectClose = () => {
        setSelectUp(null);
    };

    const theReturnValue = state.returnValue

    var menuitems: ReactElement[] = []
    if (commands.length === 0) {
        const jsx = (
            <Box key={0} sx={{ display: 'flex' }}>
                <CircularProgress />
            </Box>
        )
        menuitems.push(jsx)
    } else {
        for (var i = 0; i < commands.length; i++) {
            const part = commands[i]
            const istr = '' + i
            const jsx = (
                <MenuItem key={istr} id={istr} onClick={handleSelectSelect}>{part}</MenuItem>
            )
            menuitems.push(jsx)
        }
    }

    function getThingInfo() {
        if ( info.pubk === '' && info.adminHint === '') {
            return (
                <p className='error'>offline</p>
            )
        }
        return (
            <>
                {/* <p>About: {info.about}</p>
                <p>Public key: {info.pubk}</p>
                <p>Admin hint: {info.adminHint}</p> */}
            </>
        )
    }

    function makeArgs() {
        var args: ReactElement[] = []
        for (i = 0; i < state.config.cmdArgCount; i++) {
            const ele = (
                <span className='arg'>
                    <TextField
                        key={i}
                        id={'' + i}
                        className='cmdArg'
                        onChange={argTextClicked}
                        onBlur={() => { }} // adminKeyBlur argTextClicked
                        label='Add text here'
                        defaultValue={''}
                        helperText=""
                        disabled={false}
                    />
                </span>
            )
            args.push(ele)
        }
        return args
    }


    return (

        <Card variant="outlined" className='devCard'>
            <div className='cardRow1' >
                <span className='commandInputSpan'>
                    <TextField
                        onChange={textClicked}
                        onBlur={changeThingName}
                        // id="outlined-helperText"
                        label={"Thing name"}
                        defaultValue={state.config.longname}
                        helperText=""
                        fullWidth
                    // disabled={disabled}
                    />
                </span>
                <span className='moreDevInfoWrapper'>
                    <div className='moreDevInfoSpan'>
                        {getThingInfo()}
                    </div>
                </span>
                {/* {props.showPubkey ? (<span className='devKey'>{txtStandin('Thing key', 'iuFO975k...')}</span>) : null}
                {props.showAdminKey ? (<span className='adminKey'>{txtStandin('Admin key', 'oGeJoMdC...')}</span>) : null} */}
            </div>
            <div className='cardRow2'>
                <span className='cmdpulldown'  ><ArrowDropDownIcon onClick={handleSelectClick} style={{ fontSize: "48px" }} /></span>

                <span className='cmdSpan'>
                    <div className='bottomoverlay' >
                        {state.config.cmdDescription}
                    </div>
                    <div className='overlay' >
                        Command
                    </div>
                    {/* <span className='cmd' >{txtStandin('The command','get time',true)}</span> */}
                    <button type="button" onClick={doCommandAttempt} className='cmdButton' >{state.config.commandString}</button>

                </span>
                {/* {makeArgs()} */}
                <span className='resultSpan'>
                    <div className='overlay' >
                        Result
                    </div>
                    <div className='resultDiv' >
                        {helpers.LinesToParagraphs(theReturnValue)}
                    </div>
                </span>
                <span className='menu'><MenuIcon onClick={handleMenuClick} style={{ fontSize: "36px" }} /></span>
            </div>
            <div className='cardRow2'>
                {makeArgs()}
            </div>
            <Menu
                id="simple-menu"
                anchorEl={menuUp}
                keepMounted
                open={Boolean(menuUp)}
                onClose={handleClose}
            >

                <MenuItem onClick={handleMenuDelete}>Delete</MenuItem>
                <MenuItem onClick={handleMenuDuplicate}>Duplicate</MenuItem>
                <MenuItem onClick={handleMenuUp}>Move up</MenuItem>
                <MenuItem onClick={handleMenuDown}>Move down</MenuItem>

            </Menu>

            <Menu
                id="simple-menu"
                anchorEl={selectUp}
                keepMounted
                open={Boolean(selectUp)}
                onClose={handleSelectClose}
            >
                {menuitems}

            </Menu>

        </Card>

    )

}

